<template>
  <div style=" _margin-left:20px; position: relative;" class="new-tabs1 new-tabs2" >
    <!--   多标签样例-->
    <!--    <el-card :style="{width: windowWidth}" >-->
    <!--      <el-breadcrumb separator-class="el-icon-arrow-right" style="padding:5px;">-->
    <!--        &lt;!&ndash;      <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>&ndash;&gt;-->
    <!--        <el-breadcrumb-item-->
    <!--          v-for="(item, index)  in this.$route.query.caption"-->
    <!--          :key="index"-->
    <!--        >{{ customGlobal.decrypt(item,customGlobal.cryptoKey) }}</el-breadcrumb-item>-->
    <!--      </el-breadcrumb>-->
    <!--    </el-card>-->

    <!--    <el-button type="primary" @click="showQuery">确定</el-button>-->
    <el-select v-model="selectValue1"  placeholder="请选择" :popper-append-to-body="false"   @change="changeSel($event)" style="position:absolute;left:10px;top:0px;width:150px;z-index: 999;" class="new-select1 new-select2 new-select3" >
      <!--      <el-option-->
      <!--        v-for="item in options"-->
      <!--        :key="item.value"-->
      <!--        :label="item.label"-->
      <!--        :value="item.value">-->
      <!--      </el-option>-->
      <el-option
        key="1"
        label="室温采集"
        value="1"
      >
      </el-option>
      <el-option
        key="2"
        label="温度面积法"
        value="2"
      >
      </el-option>
      <el-option
        key="3"
        label="二网平衡"
        value="3"
      >
      </el-option>
    </el-select>

    <el-tabs  v-model="editableTabsValue"  :style="{ height: '200px'}"  @tab-click="trigerChildObj"  >





      <el-tab-pane
        v-for="(item, index) in editableTabs"
        :key="item.name"
        :label="item.title"
        :name="item.name"


      >
        <component

          v-if=vueObj[index].show
          :is=item.content
          :pri_detail="{
            treeNodeCaptions:$route.query.caption,
            treeNodeIds:$route.query.id,
            treeNodeOrgId:$route.query.orgid,
            currentTreeNode:$route.query.cur_obj,
            treeNodeType:$route.query.type,
            cbInfo:$route.query.cb_info

            }"
          @parent_method="showPublicMsg"
        >

        </component>
      </el-tab-pane>
    </el-tabs>
<!--    <community-builder-set-info ref="cb"></community-builder-set-info>-->
    <device-info-community-builder-set-info ref="cb"></device-info-community-builder-set-info>
  </div>

</template>

<script>


import DeviceInfoCommunityBuilderSetInfo from "../baseInfoManager/DeviceInfoCommunityBuilderSetInfo";

export default {
  name: "DeviceInfoCenter",
  components:{DeviceInfoCommunityBuilderSetInfo},

  mounted() {

  },
  created(){
    console.info("传递参数："+this.$route.query);
    this.init();

  },
  data() {
    return {
      selectValue1:'1',
      editableTabsValue: '1',
      editableTabs:[],
      vueObj :[
        {caption:'NB传感器',id:'1',vue:'/views/deviceManager/DeviceInfoGateWayExample.vue',show:true}
      ],

    }
  },
  watch: {


  },

  methods:{
    showPublicMsg( args,callback){
      // console.info("返回对象："+JSON.stringify(  this.$refs.cb.showPublicInfo() ));
      // return  this.$refs.cb.showPublicInfo();
      callback(this.$refs.cb.showPublicInfo());
    },
    showQuery(){
      console.info("this.$route.query:"+this.$route.query.orgid);
      // console.info("this.$route.query:"+decodeURIComponent(this.$route.query.caption));
      console.info("this.$route.query:"+this.customGlobal.decrypt(this.$route.query.caption,this.customGlobal.cryptoKey));
      console.info("this.$route.query:"+this.$route.query.id);
    },
    init(){
      this.editableTabs = [];
      for(var i1=0;i1<this.vueObj.length;i1++){
        //console.info("obj:"+this.vueObj[i1].vue);
        let vue = this.vueObj[i1].vue;

        let Acomponent  = () => import('@/components' +  vue).catch((error) => {
          //console.info("出错了："+error);
          this.$message({
            type: 'error',
            showClose: true,
            duration:0,
            dangerouslyUseHTMLString: true,
            // iconClass:'el-icon-info',
            message: '<spand style="color:black;">出错了：' + error +"</spand>"
          });
        });


        this.editableTabs.push({
          title: this.vueObj[i1].caption,
          name: this.vueObj[i1].id,
          content:Acomponent
        });
      }
    },
    trigerChildObj(tab, event){
      console.info("tab.name:"+tab.name);
      // console.log(tab, event);
      // console.info("editableTabsValue:"+this.editableTabsValue);



      for (var i1=0;i1<this.vueObj.length;i1++){
        this.vueObj[i1].show = false;
      }
      this.vueObj[tab.name-1].show = true;


    },
    changeSel(e){
      console.info("当前选择："+this.selectValue1);
      if(this.selectValue1==='1'){
        this.vueObj =[
          {caption:'NB传感器',id:'1',vue:'/views/deviceManager/DeviceInfoGateWayExample.vue',show:true}
        ]
      }else if(this.selectValue1==='2'){
        this.vueObj =[
          {caption:'采集计算器',id:'1',vue:'/views/deviceManager/DeviceInfoGateWayExample.vue',show:true},
          {caption:'智能温控阀',id:'2',vue:'/views/deviceManager/DeviceInfoGateWayExample.vue',show:false},
          {caption:'温度传感器',id:'3',vue:'/views/deviceManager/DeviceInfoGateWayExample.vue',show:false},
          {caption:'楼栋热量表',id:'4',vue:'/views/deviceManager/DeviceInfoGateWayExample.vue',show:false}
        ]
      }else if(this.selectValue1==='3'){
        this.vueObj =[
          {caption:'二网平衡阀',id:'1',vue:'/views/deviceManager/DeviceInfoGateWayExample.vue',show:true},

        ]
      }
      this.editableTabsValue = '1';
      this.init();

    },


  }
}
</script>

<style scoped>
.new-tabs1 /deep/ .el-tabs__item{
  width:100px!important;
  text-align: center!important;
}
.new-tabs2 /deep/ .el-tabs__nav-scroll{
  padding-left: 200px!important;
}
.new-select1 /deep/ .el-input{
  font-size: 14px !important;
}
.new-select2 /deep/ .el-input--suffix .el-input__inner {
  color: rgb(255, 255, 255) !important;
  background: rgb(75, 134, 188) !important;
}
.new-select3 /deep/ .el-select-dropdown__item.selected {
  color: rgb(75, 134, 188)!important;
}
</style>



